import formBudget from "./formBudget";
import { POST, DELETE } from '../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, deleteData, error } from "../../service/MessageBox"

export default {
  name: "client",
  components: { formBudget, HalfCircleSpinner },
  data() {
    return {
      isLoading: false,
      isShow: false,
      pagination: {
        page: 1,
        rowsPerPage: 5
      },
      totalData: 0,
      disableView: false,
      disableEdit: false,
      textTitle: "",
      dataBudget: [],
      form: {
        Id: null,
        BudgetCode: null,
        TypeCost: null,
        DescriptionCost: null,
        JumlahTenagaAhli: null,
        Quantity: null,
        SatuanVolume: null,
        HargaSatuan: null,
      },
      headers: [{
        text: "Aksi",
        value: "aksi",
        sortable: false,
        align: "left",
        class: ""
      }, {
        text: "Budget Code",
        value: "BudgetCode",
        align: "center"
      }, {
        text: "Type Cost",
        value: "TypeCost",
        align: "center"
      }, {
        text: "Description Cost",
        value: "DescriptionCost",
        align: "center"
      }, {
        text: "Jumlah Tenaga Ahli",
        value: "JumlahTenagaAhli",
        align: "center"
      }, {
        text: "Quantity",
        value: "Quantity",
        align: "center"
      }, {
        text: "Satuan Volume",
        value: "SatuanVolume",
        align: "center"
      }, {
        text: "Harga Satuan",
        value: "HargaSatuan",
        align: "center"
      }]
    };
  },
  created() {
    this.loadData()
  },
  beforeMount: function () { },
  computed: {},
  methods: {
    toggleInputDialog: function () {
      this.isShow = !this.isShow;
      this.textTitle = "Tambah Budget"
    },
    onClose: function () {
      this.isShow = false;
      this.disableEdit = false;
      this.disableView = false;
      this.resetForm()
    },
    loadData: function () {
      try {
        this.isLoading = true
        const { page, rowsPerPage } = this.pagination;
        POST(`budget/budget_cr/${page}/${rowsPerPage}`).then((res) => {
          this.dataBudget = res.Data.map(x => {
            return ({
              Id: x.Id,
              BudgetCode: x.BudgetCode,
              TypeCost: x.TypeCost,
              DescriptionCost: x.DescriptionCost,
              JumlahTenagaAhli: x.JumlahTenagaAhli,
              Quantity: x.Quantity,
              SatuanVolume: x.SatuanVolume,
              HargaSatuan: x.HargaSatuan,
            })
          })
          this.totalData = res.TotalData;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onEdit: function (id) {
      try {
        this.isLoading = true
        const { page, rowsPerPage } = this.pagination;
        POST(`budget/budget_cr/${page}/${rowsPerPage}`, { Id: id }).then((res) => {
          this.form = res.Data[0]
          this.disableView = false;
          this.disableEdit = true;
          this.textTitle = "Ubah Budget"
          this.isShow = true;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    onDelete: function (id) {
      deleteData(id, this.delData)
    },
    delData: function (id) {
      try {
        this.isLoading = true
        DELETE(`budget/${id}`).then((res) => {
          success("Data Berhasil Dihapus")
          this.loadData()
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onView: function (id) {
      try {
        this.isLoading = true;
        const { page, rowsPerPage } = this.pagination;
        POST(`budget/budget_cr/${page}/${rowsPerPage}`, { Id: id }).then((res) => {
          this.form = res.Data[0]
          this.disableView = true;
          this.isShow = true;
          this.textTitle = "Lihat Budget"
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    resetForm: function () {
      this.form = {
        Id: null,
        BudgetCode: null,
        TypeCost: null,
        DescriptionCost: null,
        JumlahTenagaAhli: null,
        Quantity: null,
        SatuanVolume: null,
        HargaSatuan: null,
      }
    }
  },
  watch: {
    pagination: {
      handler() {
        try {
          this.isLoading = true;
          const { page, rowsPerPage } = this.pagination;
          POST(`budget/budget_cr/${page}/${rowsPerPage}`).then((res) => {
            this.dataBudget = res.Data.map(x => {
              return {
                Id: x.Id,
                BudgetCode: x.BudgetCode,
                TypeCost: x.TypeCost,
                DescriptionCost: x.DescriptionCost,
                JumlahTenagaAhli: x.JumlahTenagaAhli,
                Quantity: x.Quantity,
                SatuanVolume: x.SatuanVolume,
                HargaSatuan: x.HargaSatuan,
              };
            });
            this.isLoading = false;
            this.totalData = res.TotalData;
          });
        } catch (err) {
          this.isLoading = false;
          error("Error");
        }
      },
      deep: true
    }
  }
};
