import { POST, GET, PUT, DELETE } from '../../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, error } from "../../../service/MessageBox"

export default {
  name: "input-client",
  components: { HalfCircleSpinner },
  props: ["form", "onClose", "loadData", "textTitle", "disableView", "disableEdit"],
  data() {
    return {
      isLoading: true
    };
  },
  beforeMount: function () { },
  mounted() { },
  methods: {
    onSave: function () {
      try {
        this.isLoading = true
        let dataReq = {
          Id: this.form.Id,
          BudgetCode: this.form.BudgetCode,
          TypeCost: this.form.TypeCost,
          DescriptionCost: this.form.DescriptionCost,
          JumlahTenagaAhli: this.form.JumlahTenagaAhli,
          Quantity: this.form.Quantity,
          SatuanVolume: this.form.SatuanVolume,
          HargaSatuan: this.form.HargaSatuan,
        }
        if (dataReq.Id === null) {
          POST("budget", dataReq).then((res) => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Disimpan")
            this.onClose();
          })
        } else {
          PUT("budget", dataReq).then((res) => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Di Update")
            this.onClose();
          })
        }
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
  }
};
